<template>
  <div id="box2">
    <div class="top">
      <div class="f20">订单收入:￥{{$route.query.zong}}</div>
    </div>
    <div class="tab-container">
      <van-tabs type="card">
        <van-tab title="收费订单">
          <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
            <div class="list">
              <div v-for="(item,i) in list" :key="i" class="bbg">
                <div class="item">
                  <span>酒店名称：{{item.hotel_name}}</span>
                  <span>收益：{{item.pay_price}}</span>
                </div>
                <div class="item">
                  <span>时间：{{item.pay_time}}</span>
                  <span>设备号：{{item.number}}</span>
                </div>
              </div>
            </div>
          </van-list>
        </van-tab>
        <van-tab title="免费订单">
          <van-list v-model="loading2" :finished="finished2" finished-text="没有更多了" @load="getFreeOrder">
            <div class="list">
              <div v-for="(item,i) in list2" :key="i" class="bbg">
                <div class="item">
                  <span>酒店名称：{{item.hotel_name}}</span>
                  <span>收益：{{item.pay_price}}</span>
                </div>
                <div class="item">
                  <span>时间：{{item.pay_time}}</span>
                  <span>设备号：{{item.number}}</span>
                </div>
              </div>
            </div>
          </van-list>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import { gettotal_order_list, getFreeOrderList } from "@/axios/getData";
import { List, Tab, Tabs } from "vant";
export default {
  data() {
    return {
      page: 1,
      list: [],
      finished: false,
      loading: false,
      count: "", // 总数
      page2: 1,
      list2: [],
      finished2: false,
      loading2: false,
      count2: ""
    };
  },
  components: {
    vanList: List,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  created() {
    // this.getdata()
  },
  methods: {
    async onLoad() {
      let arrs = JSON.parse(localStorage.getItem("shebei_arr"));
      if (this.loading) {
        var res = await gettotal_order_list({
          arras: arrs.join(","),
          page: this.page
        });
        console.log(res.data);
        this.count = res.data.count;
        if (this.page == 1) {
          this.list = res.data.list;
        } else {
          this.list = this.list.concat(res.data.list);
        }
        this.loading = false;
        this.page++;
        if (this.list.length >= this.count) {
          this.finished = true;
        }
      }
    },
    async getFreeOrder() {
      let arrs = JSON.parse(localStorage.getItem("shebei_arr"));
      var res = await getFreeOrderList({
        arras: arrs.join(","),
        page: this.page2
      });
      console.log(res.data);
      this.count = res.data.count;
      if (this.page2 == 1) {
        this.list2 = res.data.list;
      } else {
        this.list2 = this.list2.concat(res.data.list);
      }
      this.loading2 = false;
      this.page2++;
      if (res.data.list.length === 0) {
        this.finished2 = true;
      }
      // 加载状态结束
      this.loading2 = false;
    }
  },
  mounted() {}
};
</script>

<style lang="scss">
#box2 {
  height: 100vh;
  background: #fff;

  .top {
    width: 100%;
    height: 124px;
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: -webkit-linear-gradient(45deg, #2fe0ef, #15bddb);
    .info {
      display: flex;
      align-items: center;
      .span {
        display: inline-block;
        margin-right: 10px;
      }
    }
  }
  .img {
    margin-left: 15px;
    width: 32px;
    height: 32px;
  }
  .list {
    padding: 0 20px;
    color: #333;
    .item {
      display: flex;
      justify-content: space-between;
      padding: 20px 0;
      // border-bottom: 1px solid #F4F4F4;
    }
    .bbg {
      border-bottom: 1px solid #f4f4f4;
    }
  }

  .tab-container {
    margin-top: -2px;

    .van-tabs--card {
      padding-top: 60px;
    }
    .van-tabs__wrap {
      height: 76px;
      background: -webkit-linear-gradient(45deg, #2fe0ef, #15bddb);

      .van-tabs__nav {
        margin-top: 4px;
      }
      .van-tabs__nav--card {
        border: 2px solid #3aaded;
      }
      .van-tabs__nav--card .van-tab {
        color: #3aaded;
        border-right: 1px solid #3aaded;
      }
      .van-tabs__nav--card .van-tab.van-tab--active {
        color: #fff;
        background-color: #3aaded;
      }
    }
  }
}
</style>



